.about {
  /* Keep width/padding in sync with .intro in Intro.css */
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  max-width: 840px;
  margin: 50px auto 0;
  padding: 0 40px 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;

  /* fonts */
  font-weight: 300;
  line-height: 1.8em;
}

.about-block {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 100px auto;
}

.about-block.block-A {
  padding-top: 50px;
  margin-bottom: 50px;
}

.about-block.image-block {
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-pack: start;
      justify-content: flex-start;
  /* NOTE: Use negative margin of 10px since the images have 10px padding */
  margin: 0 -10px 100px;
}

.about-caption {
  word-wrap: break-word;
  max-width: 500px;
}

.about-caption p {
  font-size: 15px;
}

/* IMAGES */

.about-image-wrapper {
  max-width: 300px;
}



